<template>
  <section class="open-wrap">
      <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
      <el-collapse v-model="activeName" class="open-wrap-content">
        <h1 class="open-api-title">{{ apichname }}</h1>
        <p class="open-api-detail">{{ desc }}</p>
        <el-collapse-item :title="item.title" :name="index" v-for="(item, index) in apiForm">
          <p v-if="item.type">{{item.data}}</p>
          <!--<el-input type="textarea" :rows="2" v-model="item.data" v-if="item.type">
          </el-input>-->
          <!--<pre class="brush: js" v-if="item.type">{{item.data}}</pre>
          <code v-if="item.type">{{item.data}}</code>-->
          <el-table :data="item.data" border class="open-api-detail" v-else>
            <el-table-column :prop="key" :label="val" v-for="(val, key) in item.head"></el-table-column>
          </el-table>
        </el-collapse-item>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="open-form" v-if="isCanState">
          <el-form-item label="审核结果" class="open-form-input" prop="checkState">
            <el-radio-group v-model="ruleForm.checkState">
              <el-radio v-for="(state, key) in checkState" class="radio" :label="key" v-if="key==constans['STATE-PASS'] || key==constans['STATE-NOPASS']">{{state}}</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="备注" prop="checkMem" class="open-form-input" v-if="ruleForm.checkState == constans['STATE-NOPASS']">
            <el-input v-model="ruleForm.checkMem" placeholder="请输入备注"></el-input>
          </el-form-item>
          <el-form-item class="open-center" v-if="checkState">
            <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm')" v-if="ruleForm.checkState == constans['STATE-PASS']">提交并发布</el-button>
            <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm')" v-else>提交</el-button>
            <!--<el-button class="open-login-btn" type="primary" @click="applyForm('ruleForm')">发布</el-button>-->
          </el-form-item>
        </el-form>
      </el-collapse>
    </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  import userService from '@/api/userService.js'

  export default{
    name: 'api-detail',
    data () {
      return {
        constans: constans, // 常量
        navData: config.navLeft,
        targetName: 3,
        isCanState: this.$route.query.isCanState,
        apiid: this.$route.query.apiid,
        states: config.openState,
        apichname: '',
        desc: '',
        appid: '',
        apiForm: [
        ],
        activeName: ['1'],
        // groups: [],
        requireds: config.requireds,
        openState: config.openState,
        checkState: config['checkState'],
        ruleForm: {
          apiId: this.$route.query.apiid,
          checkState: '3',
          checkMem: ''
        },
        rules: {
          checkState: {
            required: true,
            trigger: 'blur',
            message: '请选择审核状态'
          },
          checkMem: [{
            required: true,
            trigger: 'blur',
            message: '请填写审核失败的原因'
          }]
        },
        gwRate: config.gwRate // 网关限频
      }
    },
    created () {
      // 获取api详情
      userService.getResponse('showapidetail', {apiid: this.apiid})
        .then((response) => {
          let oData = response.data
          let message = oData.message
          let nCode = oData.code
          if (nCode === '1') {
          let apiDetail = oData.data
          this.formatData(apiDetail)
        } else {
            this.$message.error(message)
            this.$router.go(-1)
        }
      })
      .catch(error => {
        this.$message.error(error)
        this.$router.go(-1)
      })
      // 获取功能组
      /* this.$ajax(config.url.groupList, {categoryId: this.categoryId}, config.method.groupList)
        .then((response) => {
        let oData = response.data
        let message = oData.message
        let nCode = response.code
        if (nCode === '1') {
        let dataList = oData.data
        let allData = dataList.items
        allData.forEach((item) => {
          this.groups.push({
          id: item.id,
          name: item.appname
        })
      })
      } else {
          this.$message.error(message)
      }
    })
    .catch(error => {
      this.$message.error(error)
      }) */
    },
    methods: {
      formatData (data) {
        this.apichname = data.apichname
        this.desc = data.desc
        this.appid = data.appid
        let gatewayUrls = data.gatewayUrls && data.gatewayUrls[0]
        let apienname = data.apienname
        let appname = data.appname
        let httpmethod = data.httpmethod
        let testendpoint = data.testendpoint
        let endpoint = data.endpoint
        let state = data.state
        let headers = data.headers
        let args = data.args
        let result = data.result
        let codes = data.errcodes
        let exname = data.exname
        let extype = data.extype
        let excode = data.statecode
        let exvalue = data.retexample.exvalue
        let per = data.per
        let rate = data.rate
        let quota_max = data.quota_max
        let quota_renewal_rate = data.quota_renewal_rate
        let version = data.apiversion.version
        let versionRemark = data.apiversion.versionRemark
        let apiDetail = [
          {
            title: '公共参数',
            head:{
              name: '参数名称',
              value: '参数值'
            },
            data: [{
              'name': 'Http请求地址',
              'value': gatewayUrls
            }, {
              'name': 'Http方法',
              'value': httpmethod
            }, {
              'name': '服务分类',
              'value': appname
            }, {
              'name': '正式URL',
              'value': testendpoint
            }/* , {
             'name': '正式URL',
             'value': endpoint
             } */, {
              'name': '状态',
              'value': config['openState'][state]
            }, {
              'name': '频次',
              'value': rate + '次/秒'
            }/*, {
              'name': '频次周期',
              'value': rate + 's'
            }*/, {
              'name': '最大配额',
              'value': quota_max + '次'
            }, {
              'name': '配额重置周期',
              'value': this.gwRate.quotaRenewalRate[quota_renewal_rate]
            }, {
              'name': '版本号',
              'value': version
            }, {
              'name': '版本描述',
              'value': versionRemark
            }]
          }, {
            title: 'Header参数',
            head:{
              argname: '参数名称',
              argtype: '参数选项',
              required: '是否必须',
              desc: '示例值',
              example: '描述'
            },
            data: this.convertData(headers)
          }, {
            title: '应用参数',
            head:{
              argname: '参数名称',
              argtype: '参数选项',
              required: '是否必须',
              desc: '示例值',
              example: '描述'
            },
            data: this.convertData(args)
          }, {
            title: '返回类型',
            head:{
              retname: '参数名称',
              rettype: '参数选项',
              desc: '示例值',
              example: '描述'
            },
            data: result
          }, {
            title: '返回示例',
            type: 'textarea',
            data: exvalue
          }, {
            title: '错误代码',
            head:{
              errname: '错误码',
              desc: '描述'
            },
            data: codes
          },/* {
            title: '频次限制',
            head:{
              name: '名称',
              value: '值'
            },
            data: [{
              'name': '频次',
              'value': per + '次'
            }, {
              'name': '频次周期',
              'value': rate + 's'
            }, {
              'name': '最大配额',
              'value': quota_max
            }, {
              'name': '配额重置周期',
              'value': quota_renewal_rate
            }]
          } */
        ]
        /*apiDetail.push({
          title: '版本信息',
          head:{
            name: '参数名称',
            value: '参数值'
          },
          data: [{
            'name': '版本号',
            'value': version
          }, {
            'name': '版本描述',
            'value': versionRemark
          }]
        })*/
        this.apiForm.push(...apiDetail)
        console.log(this.apiForm)
      },
      convertData (data) {
        data.forEach(function (item) {
          let required = item.required
          item.required = config['requireds'][required]
        })
        return data
      },
      submitForm (ruleForm) {
        this.$refs[ruleForm].validate((valid) => {
          if (valid) {
            this.auditGroup() // 审核
          }
        })
      },
      //审核
      auditGroup () {
        userService.getResponse('auditApi', this.ruleForm)
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            if (status === 'success') {
              this.$message.success('审核成功')
              this.$router.push({
                name: 'apiMan'
              })
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            this.$message.error(error)
          })
      }
      // 发布
      /*applyApi () {
        userService.getResponse('applyApi', {apiid: this.apiid})
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              this.$message.success('发布成功')
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            this.$message.error(error)
          })
      },*/
    },
    components: { compontLeft }
  }
</script>



