import config from './config.js'
import ajax from './api.js'
/*
所有网关的api
 */
class GwService {
  constructor () {
    this.gwListUrl = config.gwList.url // 网关列表
    this.gwListMethod = config.gwList.method
    this.nodeListUrl = config.nodeList.url // 节点列表
    this.nodeListMethod = config.nodeList.method
    this.addNodeUrl = config.addNode.url // 添加节点
    this.addNodeMethod = config.addNode.method
    this.modNodeUrl = config.modNode.url // 修改节点
    this.modNodeMethod = config.modNode.method
    this.delNodeUrl = config.delNode.url // 删除节点
    this.delNodeMethod = config.delNode.method
  }
  /* async getResponse (type, data) {
    let response
    let apiType = config[type] || {url: '', method: 'POST'}
    try {
      response = await ajax(apiType.url, data, apiType.method)
    } catch (e) {
      console.log(e)
    }
    return response
  } */
  async getGwList (currentPage = '', pageSize = '') {
    let gwList
    try {
      gwList = await ajax(this.gwListUrl, {currentPage, pageSize}, this.gwListMethod)
    } catch (e) {
      console.log(e)
    }
    return gwList
  }
  async getNodeList (colId, currentPage = '', pageSize = '') {
    let nodeList
    try {
      nodeList = await ajax(this.nodeListUrl, {colId, currentPage, pageSize}, this.nodeListMethod)
    } catch (e) {
      console.log(e)
    }
    return nodeList
  }
  async addNode (data) {
    let response
    try {
      response = await ajax(this.addNodeUrl, data, this.addNodeMethod)
    } catch (e) {
      console.log(e)
    }
    return response
  }
  async modNode (data) {
    let response
    try {
      response = await ajax(this.modNodeUrl, data, this.modNodeMethod)
    } catch (e) {
      console.log(e)
    }
    return response
  }
  async delNode (nodeId) {
    let response
    try {
      response = await ajax(this.delNodeUrl, {nodeId}, this.delNodeMethod)
    } catch (e) {
      console.log(e)
    }
    return response
  }
}
export default new GwService()



