import Vue from 'vue'
import Router from 'vue-router'
import constans from '@/assets/js/constans.js'
import header from '@/components/HEAD.vue' // 首页
import compontCurmbs from '@/components/CURMBS.vue'
import footer from '@/components/FOOTER.vue'
import login from '../views/login.vue' // 登录

// import signUp from '../views/signUp.vue' // 注册
import gwList from '@/views/personCenter/gwList.vue'
import nodeList from '@/views/personCenter/nodeList.vue'
import supplyMan from '@/views/personCenter/supplyMan.vue' // 用户管理
import groupMan from '@/views/personCenter/groupMan.vue' // 分类管理
import apiMan from '@/views/personCenter/apiMan.vue' // api管理
import apiDetail from '@/views/personCenter/apiDetail.vue' // api详情

Vue.use(Router)

let router = new Router({
  // base: '../',
  // mode: 'history',
  routes: [
    /* {
      path: '*',
      redirect: {
        name: 'gwList'
      }
    }, */
    {
      path: '/',
      redirect: {
        name: 'login'
      }
    }, {// 登录
      path: '/login',
      name: 'login',
      component: login,
      meta: {
        title: '登录'
      }
    }, {
      path: '/openCenter/supplyMan',
      name: 'supplyMan',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: supplyMan,
        footer: footer
      },
      meta: {
        title: 'API提供者管理',
        userType: constans['SUPPLY-USER'],
        index: 0,
        curmbsData: [{
          title: 'API提供者列表',
          path: '/openCenter/supplyMan'
        }],
        needToLogin: true
      }
    }, {
      path: '/openCenter/developerMan',
      name: 'developerMan',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: supplyMan,
        footer: footer
      },
      meta: {
        title: '开发者管理',
        userType: constans['SERVICE-USER'],
        index: 1,
        curmbsData: [{
          title: '开发者管理',
          path: '/openCenter/developerMan'
        }],
        needToLogin: true
      }
    }, {
      path: '/openCenter/groupMan',
      name: 'groupMan',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: groupMan,
        footer: footer
      },
      meta: {
        title: '服务分类管理',
        curmbsData: [{
          title: '服务分类管理',
          path: '/openCenter/groupMan'
        }],
        needToLogin: true
      }
    }, {
      path: '/openCenter/apiMan',
      name: 'apiMan',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: apiMan,
        footer: footer
      },
      meta: {
        title: 'api管理',
        curmbsData: [{
          title: 'api管理',
          path: '/openCenter/groupMan'
        }],
        needToLogin: true
      }
    }, {
      path: '/openCenter/apiDetail',
      name: 'apiDetail',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: apiDetail,
        footer: footer
      },
      meta: {
        title: 'api详情',
        curmbsData: [{
          title: 'api管理',
          path: '/openCenter/groupMan'
        }, {
          title: 'api详情',
          path: '/openCenter/apiDetail'
        }],
        needToLogin: true
      }
    }, {
      path: '/openCenter/gwList',
      name: 'gwList',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: gwList,
        footer: footer
      },
      meta: {
        title: '集群列表',
        curmbsData: [{
          title: '集群列表',
          path: '/openCenter/gwList'
        }],
        needToLogin: true
      }
    }, {
      path: '/openCenter/nodeList',
      name: 'nodeList',
      components: {
        header: header,
        compontCurmbs: compontCurmbs,
        default: nodeList,
        footer: footer
      },
      meta: {
        title: '集群列表',
        curmbsData: [{
          title: '集群列表',
          path: '/openCenter/gwList'
        }, {
          title: '集群列表',
          path: '/openCenter/gwList'
        }],
        needToLogin: true
      }
    }
  ]
})

import * as types from '@/store/mutation-types.js'
import config from '@/assets/js/config.js'
// 权限拦截
router.beforeEach((to, from, next) => {
  router.app.curmbsData = to.meta.curmbsData
  let userInfo = localStorage.getItem('adminSession')
  let oUserInfo
  if (userInfo) {
    userInfo = JSON.parse(userInfo)
    let __time = userInfo.time * 1
    oUserInfo = userInfo.userInfo
    let sNowTime = new Date().getTime() * 1
    let expiredTime = config.expiredTime // 过期时间
    oUserInfo = sNowTime - __time < expiredTime ? oUserInfo : null
    router.app.$options.store.commit(types.UPDATEUSERINFO, oUserInfo)
  }
  if (!router.app.userInfo && !userInfo && !oUserInfo && to.meta.needToLogin) { // 没有用户数据且页面有权限则跳到首页
    return next({name: 'index'})
  }
  next()
})
export default router



