<template>
  <section class="open-wrap">
      <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
      <section class="open-wrap-content">
        <el-table :data="gwList" border>
          <!--<el-table-column label="编号" type="index" width="80"></el-table-column>-->
          <el-table-column label="名称" prop="colName"></el-table-column>
          <el-table-column label="代理域名" prop="colUrl"></el-table-column>
          <el-table-column label="状态" prop="colStatus" :formatter="formatter"></el-table-column>
          <el-table-column label="描述" prop="colDesc" show-overflow-tooltip></el-table-column>
          <el-table-column label="操作">
            <template scope="scope">
              <router-link class="open-list-btn" :to="{name: 'nodeList', query: {colId: scope.row.colId}}">查看</router-link>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="pageSize" class="open-page" v-if="total"></el-pagination>
      </section>
    </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  import gwService from '@/api/gwService.js'
  export default{
    name: 'gw-list',
    data () {
      return {
        navData: config.navLeft,
        targetName: 4,
        nodeState: config['gwState'],
        currentPage: 1,
        pageSize: 10,
        gwList: [],
        total: 0,
        gwState: config.gwState
      }
    },
    created () {
      this.fetch()
    },
    methods: {
      formatter (row) {
        return this.nodeState[row.colStatus]
      },
      //获取列表
      fetch () {
        gwService.getGwList().then((response) => {
            let oData = response.data
            let message = oData.message
            let status = oData.status
            if (status === 'success') {
              this.gwList = oData.data.items
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            console.log(error)
          })
      },
      handleCurrentChange (pIndex=1) {
        this.currentPage = pIndex
        this.fetch()
      },
    },
    components: { compontLeft }
  }
</script>



