<template>
  <nav class="open-control-nav">
    <!--<p class="nav-left-title" v-if="navData.title">{{ navData.title }}</p>-->
    <template v-for="(nav, index) in navData.data">
      <router-link class="nav-left-list nav-left-active" data-id="nav.id" :to="{ name: nav.routerName }" v-if="index == targetName">{{ nav.name }}</router-link>
      <router-link class="nav-left-list" :to="{ name: nav.routerName }" v-else>{{ nav.name }}</router-link>
    </template>
  </nav>
</template>
<style lang="less" scoped>
</style>
<script type='es6'>
  export default{
    name: 'left',
    data () {
      return {}
    },
    props: ['navData', 'targetName'],
    methods: {},
    components: {},
  }
</script>



