<template>
  <section class="open-wrap">
      <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
      <section class="open-wrap-content">
        <compontSearch @searchData="fetch(1, pageSize, searchRuleForm['appid'], searchRuleForm['apichname'], searchRuleForm['checkState'])" :ruleForm="searchRuleForm" :ruleData="searchRuleData"></compontSearch>
        <el-table :data="apiList" border scope="scope">
          <!--<el-table-column label="编号" type="index" width="80"></el-table-column>-->
          <el-table-column label="API名称" prop="apichname"></el-table-column>
          <el-table-column label="API版本" prop="apiversion.version"></el-table-column>
          <el-table-column label="所属分类" prop="app.appname"></el-table-column>
          <el-table-column label="提供者" prop="username"></el-table-column>
          <el-table-column label="创建日期" prop="createtime" :formatter="formatter"></el-table-column>
          <el-table-column label="状态">
            <template scope="scope">
              <el-popover trigger="hover" placement="top" v-if="scope.row.checkState*1 == constans['STATE-NOPASS']">
                <p>{{ scope.row.checkMem }}</p>
                <div slot="reference" class="name-wrapper">
                  <el-tag>{{ checkState[scope.row.checkState] }}</el-tag>
                </div>
              </el-popover>
              <p v-else>
                {{checkState[scope.row.checkState*1]}}
              </p>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template scope="scope">
              <router-link class="open-list-btn" size="small" :to="{name: 'apiDetail', query: {apiid: scope.row.id, isCanState: 1}}" v-if="scope.row.checkState==constans['STATE-UNAUDITED']">审核并发布</router-link>
             <!--<a class="open-list-btn" @click="showPop(scope.$index)">编辑</a>-->
              <router-link class="open-list-btn" :to="{name: 'apiDetail', query: {apiid: scope.row.id}}">查看</router-link>
              <a class="open-list-btn" @click="delApi(scope.$index, scope.row.id)" v-if="scope.row.checkState!=constans['STATE-PASS']">删除</a>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="pageSize" class="open-page" v-if="total"></el-pagination>
      </section>
    </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  import compontSearch from '@/components/SEARCH.vue'
  import userService from '@/api/userService.js'
  import { formatDate } from '@/assets/js/lib.js'
  export default{
    name: 'api-supplier',
    data () {
      return {
        constans: constans, // 常量
        targetName: 3,
        navData: config.navLeft,
        currentPage: 1,
        pageSize: 10,
        apiList: [],
        total: 0,
        checkState: config['checkState'],
        searchRuleForm: {
          apichname: '',
          appid: '',
          checkState: ''
        }, // 搜索
        searchRuleData: {
          apichname: {
            name: 'API名称:',
            type: 'input',
            placeholder: '请输入API名称'
          },
          checkState: {
            name: '状态:',
            type: 'select',
            data: {'': '全部', ...config['checkState']},
            placeholder: '请选择状态'
          },
          appid: {
            name: '服务分类:',
            type: 'select',
            data: [{'': '全部'}],
            isArray: true,
            placeholder: '请选择服务分类'
          }
        } // 搜索列表
      }
    },
    created () {
      this.fetch(this.currentPage, this.pageSize)
      this.getGroupList()
    },
    methods: {
      formatter (row, column) {
        return formatDate(row.createtime)
      },
      //获取列表
      fetch (currentPage, pageSize, appid = this.searchRuleForm['appid'], apichname = this.searchRuleForm['apichname'], checkState = this.searchRuleForm['checkState']) {
        userService.getResponse('apiList', {appid, apichname, checkState, currentPage, pageSize}).then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              this.apiList = oData.data.items
              this.total = oData.data.totalNumber
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            console.log(error)
          })
      },
      handleCurrentChange (pIndex=1) {
        this.currentPage = pIndex
        this.fetch(this.currentPage, this.pageSize)
      },
      // 删除
      delApi (index, apiid) {
        this.$confirm('此操作将永久删除该API, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          userService.getResponse('delApi', {apiid})
            .then((response) => {
              let oData = response.data
              let message = oData.message
              let status = oData.status
              if (status === 'success') {
                /*this.apiList.splice(index, 1)
                this.total--*/
                this.fetch(this.currentPage, this.pageSize)
              } else {
                this.$message.error(message)
              }
            })
            .catch(error => {
              this.$message.error(error)
            })
        })
          .catch()
      },
      getGroupList () {
        let pageSize = config.pageSize
        let currentPage = 1
        userService.getResponse('groupList', {pageSize: pageSize, currentPage:currentPage}).then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            let aSearchData = this.searchRuleData.appid.data
            if (nCode === '1') {
              let groupList = oData.data.items
              groupList.map(item => {
                let oGroupData = {}
                let checkState = item.checkState
                let id = item.id
                let name = item.appname
                oGroupData[id] = name
                if (checkState == constans['STATE-PASS']) {
                  aSearchData.push(oGroupData)
                }
              })
              this.total = oData.data.totalNumber
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    components: { compontLeft, compontSearch }
  }
</script>



