<template>
  <section class="open-wrap">
      <compontLeft :navData="navData" :targetName="targetName"></compontLeft>
      <section class="open-wrap-content">
        <compontSearch @searchData="fetch(1, pageSize, searchRuleForm['appname'], searchRuleForm['checkState'])" :ruleForm="searchRuleForm" :ruleData="searchRuleData"></compontSearch>
        <a class="open-group-add" @click="showPop()">添加服务分类</a>
        <el-table :data="groupList" border scope="scope">
          <!--<el-table-column label="编号" type="index" width="80"></el-table-column>-->
          <el-table-column label="分类名称" prop="appname"></el-table-column>
          <el-table-column label="分类key值" prop="appkey"></el-table-column>
          <el-table-column label="创建者" prop="username"></el-table-column>
          <el-table-column label="创建日期" prop="createData" :formatter="formatter"></el-table-column>
          <el-table-column label="状态">
            <template scope="scope">
              <el-popover trigger="hover" placement="top" v-if="scope.row.checkMem && scope.row.checkState*1 == constans['STATE-NOPASS']">
                <p>{{ scope.row.checkMem }}</p>
                <div slot="reference" class="name-wrapper">
                  <el-tag>{{ checkState[scope.row.checkState] }}</el-tag>
                </div>
              </el-popover>
              <p v-else>
                {{checkState[scope.row.checkState*1]}}
              </p>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template scope="scope">
              <a class="open-list-btn" size="small" @click="showPop(scope.$index, 1)" v-if="scope.row.checkState==constans['STATE-UNAUDITED']">审核</a>
              <a class="open-list-btn" @click="showPop(scope.$index)">编辑</a>
              <a class="open-list-btn" @click="delGroup(scope.$index, scope.row.id)">删除</a>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="pageSize" class="open-page" v-if="total"></el-pagination>
      </section>

      <el-dialog :title="title" :visible.sync="visible">
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="open-form" v-if="popType == constans['STATE-UNAUDITED']">
          <el-form-item label="审核结果" class="open-form-input" prop="checkState">
            <el-radio-group v-model="ruleForm.checkState">
              <el-radio v-for="(state, key) in checkState" class="radio" :label="key" v-if="key==constans['STATE-PASS'] || key==constans['STATE-NOPASS']">{{state}}</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="备注" prop="remark" class="open-form-input" v-if="ruleForm.checkState==constans['STATE-NOPASS']">
            <el-input v-model="ruleForm.remark" placeholder="请输入备注"></el-input>
          </el-form-item>
          <el-form-item class="open-center">
            <el-button class="open-login-btn" type="primary" @click="submitForm('ruleForm', 1)">提交</el-button>
          </el-form-item>
        </el-form>
        <!--添加编辑分组-->
        <el-form :model="groupForm" :rules="groupRules" ref="groupForm" class="open-form" v-else>
          <el-form-item label="分类名称" prop="appname" class="open-form-input">
            <el-input v-model="groupForm.appname" placeholder="请输入分类名称"></el-input>
          </el-form-item>
          <el-form-item label="分类key" prop="appkey" class="open-form-input">
            <el-input v-model="groupForm.appkey" placeholder="请输入分类key"></el-input>
          </el-form-item>
          <el-form-item class="open-center">
            <el-button class="open-login-btn" @click="visible=false">取消</el-button>
            <el-button class="open-login-btn" type="primary" @click="submitForm('groupForm')">保存</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </section>
</template>
<style lang="less" scoped>

</style>
<script type='es6'>
  import constans from '@/assets/js/constans.js'
  import config from '@/assets/js/config.js'
  import compontLeft from '@/components/LEFT.vue'
  import compontSearch from '@/components/SEARCH.vue'
  import userService from '@/api/userService.js'
  import { formatDate } from '@/assets/js/lib.js'
  export default{
    name: 'api-supplier',
    data () {
      return {
        constans: constans, // 常量
        targetName: 2,
        navData: config.navLeft,
        currentPage: 1,
        pageSize: 10,
        groupList: [],
        total: 0,
        checkState: config['checkState'],
        ruleForm: {
          id: '',
          checkState: '3',
          remark: ''
        },
        rules: {
          checkState: {
            required: true,
            trigger: 'blur',
            message: '请选择审核状态'
          },
          remark: [{
            required: true,
            trigger: 'blur',
            message: '请填写审核失败的原因'
          }]
        },
        groupForm: {
          appname: '',
          appkey: ''
        },
        title: '添加分类',
        groupRules: {
          appname: [{
            required: true,
            message: '请输入分类名称',
            trigger: 'blur'
          }, {
            message: '分类名称只能包括中文、英文、-、_、*',
            trigger: 'change, blur',
            pattern: config.validate.appName
          }],
          appkey: [{
            required: true,
            message: '请输入分类key值',
            trigger: 'blur'
          }, {
            message: '分类key值只能包括英文、数字',
            trigger: 'change, blur',
            pattern: config.validate.apiEnName
          }]
        },
        visible: false,
        index: '',
        popType: 1, // 弹窗类型
        searchRuleForm: {
          appname: '',
          checkState: ''
        }, // 搜索
        searchRuleData: {
          appname: {
            name: '名称:',
            type: 'input',
            placeholder: '请输入服务分类名称'
          },
          checkState: {
            name: '状态:',
            type: 'select',
            data: {'': '全部', ...config['checkState']},
            placeholder: '请选择状态'
          }
        } // 搜索列表
      }
    },
    created () {
      this.fetch(this.currentPage, this.pageSize)
    },
    methods: {
      formatter (row, column) {
        return formatDate(row.createDate)
      },
      //刷新路由
      /*formatterUserState (row) {
        if (row.checkState ==1) {
          return this.userState[row.state*1]
        } else {
          return '--'
        }
      },*/
      //获取列表
      fetch (currentPage, pageSize, appname = this.searchRuleForm['appname'], checkState = this.searchRuleForm['checkState']) {
        userService.getResponse('groupList', {currentPage, pageSize, appname, checkState}).then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              this.groupList = oData.data.items
              this.total = oData.data.totalNumber
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            console.log(error)
          })
      },
      handleCurrentChange (pIndex=1) {
        this.currentPage = pIndex
        this.fetch(this.currentPage, this.pageSize)
      },
      // 删除
      delGroup (index, id) {
        this.$confirm('此操作将永久删除该服务分类, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          userService.getResponse('delGroup', {id})
            .then((response) => {
              let oData = response.data
              let message = oData.message
              let nCode = oData.code
              if (nCode === '1') {
                this.groupList.splice(index, 1)
                this.total--
              } else {
                this.$message.error(message)
              }
            })
            .catch(error => {
              this.$message.error(error)
            })
        }).catch()
      },
      //打开弹窗
      showPop (nIndex, nType) {
        this.visible = true
        this.index = nIndex
        let targetData = this.groupList[nIndex]
        if (targetData) {
          if (nType) { // 审核
            this.title = '审核'
            this.ruleForm.id = targetData.id
            this.ruleForm.checkState = '3'
            this.ruleForm.remark = ''
            this.popType = constans['STATE-UNAUDITED']
          } else { // 编辑
            this.groupForm.appname = targetData.appname
            this.groupForm.appkey = targetData.appkey
            this.groupForm.id = targetData.id
            this.title = '修改服务分类'
            this.popType = constans['STATE-UNCOMMITTED']
          }
        } else { // 添加
          this.popType = constans['STATE-UNCOMMITTED']
          this.groupForm = {
            appname: '',
            appkey: ''
          }
          this.title = '添加服务分类'
        }
      },
      submitForm (ruleForm, nType) {
        this.$refs[ruleForm].validate((valid) => {
          if (valid) {
            if (this.groupForm.id || this.ruleForm.id ) {
              nType && this.auditGroup() // 审核
              !nType && this.modGroup() // 编辑
            }else{
              this.addGroup()
            }
          } else {
            // this.$message.error('保存失败!');
            return false
          }
        })
      },
      //添加功能分类
      addGroup () {
        userService.getResponse('addGroup', this.groupForm)
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              // this.groupForm.id = oData.id
              // this.groupList.length < this.pageSize-1 ? this.groupList.unshift(this.groupForm) : this.groupList.unshift(this.groupForm).pop()
              // this.total++
              this.fetch(this.currentPage, this.pageSize)
              this.visible = false
            } else {
              this.$message.error(message);
            }
          })
          .catch(error => {
            console.log(error);
          })
      },
      //修改功能分类
      modGroup () {
        let targetData = this.groupList[this.index]
        console.log('targetData', targetData)
        if (this.groupForm.appname == targetData.appname && this.groupForm.appkey == targetData.appkey) {
          this.visible = false
          return
        }
        userService.getResponse('modifyGroup', this.groupForm)
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              this.groupList[this.index].appname = this.groupForm.appname
              this.groupList[this.index].appkey = this.groupForm.appkey
              this.visible = false
              this.$message.success('修改成功')
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            console.log(error)
          })
      },
      //审核
      auditGroup () {
        userService.getResponse('auditGroup', this.ruleForm)
          .then((response) => {
            let oData = response.data
            let message = oData.message
            let nCode = oData.code
            if (nCode === '1') {
              this.visible = false
              this.groupList[this.index].checkState = this.ruleForm.checkState
              this.groupList[this.index].checkMem = this.ruleForm.remark
            } else {
              this.$message.error(message)
            }
          })
          .catch(error => {
            this.$message.error(error)
          })
      }
    },
    components: { compontLeft, compontSearch }
  }
</script>



